.alert-wrap {
  display: inline-block;
  position: relative;
  width: 100%;
}

.field-length-alert {
  background-color: #999;
  border-radius: .2em;
  color: #000;
  display: inline-block;
  font-size: .8em;
  min-width: 1em;
  opacity: .8;
  padding: .1em;
  position: absolute;
  right: .2em;
  text-align: center;
  top: 50%;
  transform: translateY(-50%);
}

.field-length-alert.-warn {
  background-color: #ff0;
}

.field-length-alert.-error {
  background-color: #f00;
}
